.wrapper {
  background: linear-gradient(
    to top,
    rgba(20, 21, 37, 0.9),
    rgba(20, 21, 37, 0.9)
  );
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list {
  color: white;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0, 0, 0, 0.75);
  width: 80%;
  margin: 0 auto;
}

.title-container {
  height: 90vh;
  background: url("design-art-2029562_1280.png"), center center no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: "Roboto Slab", serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: rgb(20, 21, 37);
  height: 90vh;
  padding-top: 80px;
  padding-left: 50px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px orange;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px rgb(20, 21, 37) inset;
  -webkit-text-fill-color: #fff !important;
}

#btnGetWeather {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 12px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 12px;
  cursor: pointer;
  background-color: teal !important;
  color: black;
  font-weight: 400;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: rgba(253, 144, 77);
  border-bottom: solid 2px rgba(255, 255, 255, 0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}

.main-heading {
  color: black !important;
  letter-spacing: 9px;
  font-size: 20px;
  font-weight: 400;
}

.main-subheading {
  color: black !important;
}

hr {
  background: black;
  width: 50%;
  float: center;
  margin-bottom: 100px;
}
